.mito-hud {
  text-align: initial;
  background: #ffffff;
  opacity: 0.95;
  box-shadow: 0 4px 6px 0 rgba(0, 0, 0, 0.2);
  border-bottom-right-radius: 2px;
  font-size: 12px;
  position: relative;
  right: 0;
  transition: right 2s; }
  .mito-hud.hidden {
    right: 100%; }
  .mito-hud .mito-hud-section {
    padding: 10px 10px; }
  .mito-hud .d-pad {
    display: grid;
    grid-template: auto auto auto / auto auto auto;
    justify-items: stretch;
    align-items: stretch;
    justify-content: center; }

.mito-hud-button {
  border-radius: 2px;
  cursor: pointer; }
  .mito-hud-button:hover .mito-hud-button-hotkey {
    top: -2px;
    left: -2px;
    box-shadow: 4px 4px 1px 0 #182026; }
  .mito-hud-button:active .mito-hud-button-hotkey {
    top: 0;
    left: 0;
    box-shadow: none; }
  .mito-hud-button .mito-hud-button-hotkey {
    font-family: 'Courier New', Courier, monospace;
    font-weight: bold;
    font-size: 16px;
    border: 1px solid #182026;
    width: 30px;
    height: 30px;
    box-shadow: 2px 2px 1px 0 #182026;
    border-radius: 5px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    position: relative;
    transition: left 0.2s, top 0.2s, box-shadow 0.2s;
    margin-right: 5px; }

.ui-popup {
  position: absolute;
  animation: popIn 0.2s both; }

@keyframes popIn {
  from {
    transform: translate(-50%, -50%) scale(0.75);
    opacity: 0; }
  to {
    transform: translate(-50%, -50%) scale(1);
    opactiy: 0.95; } }
  .ui-popup.ui-popup-top {
    top: 25%;
    left: 50%; }
  .ui-popup.ui-popup-bottom {
    bottom: 30%;
    left: 50%; }
    .ui-popup.ui-popup-bottom .connector-line {
      width: 3px;
      height: calc(7.3vh - 100%);
      background: linear-gradient(to bottom, rgba(255, 255, 255, 0.5), white);
      z-index: -1;
      position: absolute;
      left: calc(50% - 1.5px);
      bottom: 100%; }
  .ui-popup.ui-popup-left {
    left: 33%;
    top: 50%; }
  .ui-popup .popup-title {
    border-top-left-radius: 2px;
    border-top-right-radius: 2px;
    background: white;
    padding: 10px; }
  .ui-popup .popup-content {
    border-radius: 2px;
    background: white;
    box-shadow: 0 4px 6px 0 rgba(0, 0, 0, 0.2); }
  .ui-popup .popup-row {
    display: flex;
    justify-content: center; }
  .ui-popup .popup-text {
    padding: 15px; }

.popup-autoplace {
  pointer-events: none;
  opacity: 0.5; }
  .popup-autoplace .popup-content.popup-text {
    margin-top: 80%;
    padding: 5px; }
  .popup-autoplace .mito-hud-button {
    display: inline-block; }

.tutorial-content {
  font-size: 24px;
  padding: 20px; }

.mito-inventory {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  opacity: 0.95;
  transition: transform 2s;
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
  border: 1px solid grey;
  background: rgba(255, 255, 255, 0.25); }
  .mito-inventory.hidden {
    transform: translateX(-50%) translateY(170%); }
  .mito-inventory .mito-inventory-container {
    z-index: 1;
    padding: 10px;
    background: #237515;
    border-bottom: 1px solid grey;
    margin-bottom: 10px; }
  .mito-inventory .mito-inventory-indicator {
    font-weight: bold; }
  .mito-inventory .mito-inventory-water {
    color: #1a14c2; }
  .mito-inventory .mito-inventory-sugar {
    color: #e8c16d; }
  .mito-inventory .mito-inventory-maxed {
    text-align: center;
    position: absolute;
    bottom: calc(100% - 100px);
    background: #237515;
    font-weight: bold;
    padding: 5px;
    border-top-left-radius: 2px;
    border-top-right-radius: 2px;
    align-self: center;
    transition: bottom 0.2s;
    color: #ffffff; }
    .mito-inventory .mito-inventory-maxed.is-maxed {
      bottom: 100%; }
  .mito-inventory .mito-inventory-bar {
    height: 10px;
    width: 100%;
    display: flex;
    flex-direction: row; }
    .mito-inventory .mito-inventory-bar .mito-inventory-bar-water {
      background: #1a14c2;
      transition: width 0.2s; }
    .mito-inventory .mito-inventory-bar .mito-inventory-bar-sugar {
      background: #e8c16d;
      transition: width 0.2s; }
    .mito-inventory .mito-inventory-bar .mito-inventory-bar-empty {
      box-shadow: 0 5px 6px inset rgba(0, 0, 0, 0.25);
      transition: width 0.2s; }

.mito-instructions {
  width: 100%;
  height: 100%;
  position: absolute;
  background: rgba(255, 255, 255, 0.95);
  z-index: 1;
  overflow: auto;
  text-align: initial; }
  .mito-instructions .mito-instructions-container {
    max-width: 700px;
    margin: auto;
    position: relative;
    padding-bottom: 10vh; }
  .mito-instructions .esc {
    position: absolute;
    cursor: pointer;
    right: 0;
    padding: 10px;
    margin: 10px;
    color: #8a9ba8; }
    .mito-instructions .esc:hover {
      box-shadow: 2px 2px 1px rgba(16, 22, 26, 0.2); }
  .mito-instructions h1 {
    padding: 80px 0;
    font-size: 80px; }
  .mito-instructions .play-button {
    text-align: center;
    padding: 40px;
    font-size: 30px;
    cursor: pointer;
    margin: 0 auto 10vh auto;
    border: 5px solid #10161a; }
    .mito-instructions .play-button:hover {
      box-shadow: 10px 10px 5px rgba(16, 22, 26, 0.3); }
  .mito-instructions h2 {
    padding-top: 30px;
    padding-bottom: 20px; }
  .mito-instructions h1, .mito-instructions h2 {
    text-align: center; }
  .mito-instructions p {
    font-size: 16px;
    font-family: Arial, Helvetica Neue, Helvetica, sans-serif;
    margin-top: 10px; }
  .mito-instructions ol {
    -webkit-padding-start: 20px; }

.world-dom-components {
  z-index: 1;
  position: absolute; }
  .world-dom-components > * {
    pointer-events: none;
    position: absolute; }

.fruit-indicator {
  color: white;
  font-size: 12px;
  width: 100px;
  transform: translate(-50%, -50%) translateY(-30px); }

.tile-hover {
  text-align: left;
  min-width: 120px;
  min-height: 25px;
  position: absolute;
  top: 0;
  left: 200px;
  background: rgba(255, 255, 255, 0.95);
  pointer-events: none;
  border-radius: 2px;
  border: 1px solid gainsboro;
  padding: 10px;
  display: flex;
  flex-direction: column; }
  .tile-hover .info-tile-name {
    display: inline-block;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    padding-right: 5px; }
  .tile-hover .info-energy {
    font-size: 0.75em; }

.tutorial-build {
  animation: growIn 2s both;
  position: relative;
  height: 1em;
  overflow: hidden; }

@keyframes growIn {
  from {
    transform: scale(0.01, 0.01); }
  to {
    transform: scale(1, 1); } }
  .tutorial-build.tutorial-build-tissue b {
    color: darkgreen; }
  .tutorial-build.tutorial-build-root b {
    color: #b75e24; }
  .tutorial-build.tutorial-build-leaf b {
    color: yellowgreen; }

.full-page-sketch {
  width: 100vw;
  height: 100vh;
  position: relative; }

a.back-button {
  position: absolute;
  top: 0;
  left: 0;
  padding: 20px;
  color: #5c7080;
  font-size: 20px;
  font-weight: 900;
  z-index: 1; }
  a.back-button:active {
    position: absolute;
    top: 1; }

.sketch-component {
  width: 100%;
  height: 100%;
  display: flex;
  text-align: center;
  position: relative;
  user-select: none; }
  .sketch-component .sketch-error {
    animation-duration: 2s;
    animation-name: opacify-in;
    margin: auto;
    text-align: center; }
    .sketch-component .sketch-error .back {
      color: #8a9ba8;
      font-style: italic; }
  .sketch-component canvas {
    background: black;
    display: block;
    position: relative; }
    .sketch-component canvas:focus {
      outline: none; }

.sketch-elements {
  align-items: flex-start;
  display: flex;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  position: absolute;
  z-index: 1;
  visibility: hidden; }
  .sketch-elements > * {
    visibility: visible; }

@keyframes opacify-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.user-volume {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  background: none;
  color: #8a9ba8;
  border: none;
  font-size: 30px;
  width: 50px;
  height: 50px;
  line-height: 50px; }
  .user-volume:hover {
    cursor: pointer; }

* {
  box-sizing: border-box; }

h1, h2, h3, h4, h5, h6 {
  font-family: Palatino, Palatino Linotype, Palatino LT STD, Book Antiqua, serif;
  margin: 0; }

a {
  color: #182026;
  text-decoration: none;
  transition: border-bottom .05s; }
  a:hover {
    border-bottom: 3px solid #10161a; }
  a:active {
    position: relative;
    top: 1px; }

body {
  font-family: Palatino, Palatino Linotype, Palatino LT STD, Book Antiqua, serif; }

p {
  font-weight: 400;
  font-style: normal;
  font-size: 21px;
  line-height: 1.58;
  font-family: Georgia, Cambria, "Times New Roman", Times, serif; }

figure {
  margin: 0; }

h1 ~ p {
  margin-top: 10px; }

p ~ p {
  margin-top: 30px; }

#root {
  overflow: hidden; }
